.loading span {
  width: 20px;
  height: 16px;
  background-color: #fff;
  border-radius: 30px;
  animation: scale 1.5s linear infinite;
}

.loading span:nth-child(1) {
  background-color: #fff;
  animation-delay: .3s;
}

.loading span:nth-child(2) {
  background-color: #fff;
  animation-delay: .6s;
}

.loading span:nth-child(3) {
  background-color: #fff;
  animation-delay: .9s;
}

@keyframes scale {
  0% {
    transform: scaleX(.5);
  }

  50%, 75% {
    transform: scaleX(1.5);
  }

  80%, 100% {
    opacity: 0;
  }
}

