.main-button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 306px;
    height: 70px;

    border: none;
    border-radius: 16px;
    background-color: #86e543;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: calc(38 / 32);
    text-transform: uppercase;
    color: #01383a;

    cursor: pointer;

    &:hover,
    &:focus {
        box-shadow: 0px 8px 20px rgba(134, 229, 67, 0.5),
            0px 4px 10px rgba(255, 255, 255, 0.1);
    }
}

.filter-button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 168px;
    height: 51px;
    border: 1px solid #86e543;
    filter: drop-shadow(0px 4px 4px rgba(134, 229, 67, 0.2));
    border-radius: 16px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(19 / 16);
    text-align: right;
    color: #86e543;

    &:active {
        background-color: #86E543;
        color: #01383A;
    }
}